.popup-window {
  margin: 30px 0;
  box-sizing: border-box;
  background-color: $white;
}


.popup-window__header {
  @include main_font_index($color:$black, $font-size: 18px, $line-height: 26px, $font-weight: bold);
  text-align: center;
}

.popup-window__description {
  @include main_font_index($font-size: 14px, $line-height: 24px);
  text-align: center;
}

.popup-window__button {
  @include main_font_index($color:$white, $font-size: 18px, $line-height: 26px, $font-weight: bold);
  background-color: $blue;
  display: block;
}

.feedback-form__error-send {
  width: 320px;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  h2 {
    margin: 0;
    padding: 41px 0 26px;
  }

  p {
    padding: 0 50px 22px;
  }

  button {
    height: 58px;
    width: 180px;
    margin: 0 auto;
  }
}

.feedback-form__success-send {
  width: 320px;
  height: 323px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  h2 {
    margin: 0;
  }

  p {
    padding: 25px 50px 39px;
  }

  footer {
    height: 98px;
    width: 320px;
    background-color: #f2f2f2;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button {
    margin: 0;
    height: 58px;
    width: 280px;
  }
}
